  export const getCricketContestadd = (id) => {
    return (dispatch) => {
      http
        .post('/cricket/contest_prizes/list', id)
        .then((response) => {
          dispatch({
            type: 'CRICKET_CONTEST_PRIZE_ADD',
            data: response.data.data
          })
          console.log(response)
        })
        .catch((err) => {
          console.log(err)
        })
    }
  }

  export const addfile = (params) => {
    return async (dispatch) => {
      // dispatch(addPostInit())
      await http.post('/seriesUpload', params).then((response) => {
        notifySuccessadd(response.data.message)
        dispatch({
          type: 'ADD_FILE',
          data: response.data.message
        })
      }).catch((e) => {
        notifyError(e.response.data.message)
      })
    }
  }